@import url('https://fonts.googleapis.com/css2?family=Akshar:wght@300&family=Neucha&display=swap');

.NavBar {
    width: 65%;
    height: 4%;
    background-color: #EF002A;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    font-family: "Akshar", sans-serif;
    font-size: 20px;
}
.NavBarItem a {
    color: azure;
    text-decoration: none;
    display: flex;
    align-items: center;
    word-spacing: 3px;
    letter-spacing: 1px;
}
.NavBarItem {
    color: azure;
    text-decoration: none;
    display: flex;
    align-items: center;
    word-spacing: 3px;
    letter-spacing: 1px;
    cursor: pointer;
}

.NavBarIcon {
    padding-right: 6px;
    padding-bottom: 3px;
}
.NavBarItem a:hover {
    color: black;
}
.NavBarItem a:focus {
    color: black;
}
